/* ─── Quick Add FAB — Golden Premium ─── */

.quick-add-fab {
  position: fixed;
  bottom: 100px; /* Above the mode switcher */
  right: 24px;
  width: 58px;
  height: 58px;
  border-radius: 50%;
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  border: none;
  box-shadow: 
    0 4px 16px rgba(245, 158, 11, 0.35),
    0 2px 8px rgba(0, 0, 0, 0.1),
    0 0 0 1px rgba(255, 255, 255, 0.1) inset;
  cursor: pointer;
  z-index: 100;
  transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  justify-content: center;
}

.quick-add-fab:hover {
  transform: scale(1.08) translateY(-2px);
  box-shadow: 
    0 8px 24px rgba(245, 158, 11, 0.45),
    0 4px 12px rgba(0, 0, 0, 0.15),
    0 0 0 1px rgba(255, 255, 255, 0.15) inset;
}

.quick-add-fab:active {
  transform: scale(0.95);
  box-shadow: 
    0 2px 8px rgba(245, 158, 11, 0.25),
    0 1px 4px rgba(0, 0, 0, 0.1);
}

.quick-add-fab-icon {
  font-size: 30px;
  font-weight: 300;
  color: white;
  line-height: 1;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

/* ─── Modal Overlay ─── */

.quick-add-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(4px);
  z-index: 1000;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 15vh;
  animation: fadeIn 0.15s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* ─── Modal ─── */

.quick-add-modal {
  width: 90%;
  max-width: 480px;
  background: var(--bg-card);
  border: 1px solid var(--border);
  border-radius: 20px;
  box-shadow: 
    0 24px 48px rgba(0, 0, 0, 0.25),
    0 12px 24px rgba(0, 0, 0, 0.15),
    0 0 0 1px rgba(255, 255, 255, 0.05) inset;
  animation: slideUp 0.25s cubic-bezier(0.16, 1, 0.3, 1);
  overflow: hidden;
}

@keyframes slideUp {
  from { 
    opacity: 0;
    transform: translateY(20px);
  }
  to { 
    opacity: 1;
    transform: translateY(0);
  }
}

/* ─── Input ─── */

.quick-add-input {
  width: 100%;
  padding: 20px 24px;
  font-size: 18px;
  border: none;
  background: transparent;
  color: var(--text);
  outline: none;
  direction: rtl;
}

.quick-add-input::placeholder {
  color: var(--text-muted);
}

/* ─── Context Pills ─── */

.quick-add-contexts {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding: 0 20px 16px;
  border-bottom: 1px solid var(--border);
}

.quick-add-context {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  background: var(--bg-muted);
  border: 1px solid var(--border);
  border-radius: 20px;
  font-size: 13px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.15s ease;
}

.quick-add-context:hover {
  background: var(--bg-hover);
  border-color: var(--text-muted);
}

.quick-add-context.active {
  background: var(--accent);
  border-color: var(--accent);
  color: white;
}

/* ─── Actions ─── */

.quick-add-actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 20px;
  background: var(--bg-muted);
}

.quick-add-hint {
  font-size: 12px;
  color: var(--text-muted);
}

.quick-add-buttons {
  display: flex;
  gap: 8px;
}

.quick-add-cancel {
  padding: 8px 16px;
  background: transparent;
  border: none;
  font-size: 14px;
  color: var(--text-secondary);
  cursor: pointer;
  border-radius: 8px;
  transition: background 0.15s ease;
}

.quick-add-cancel:hover {
  background: var(--bg-hover);
}

.quick-add-submit {
  padding: 8px 20px;
  background: var(--accent);
  border: none;
  font-size: 14px;
  font-weight: 500;
  color: white;
  cursor: pointer;
  border-radius: 8px;
  transition: all 0.15s ease;
}

.quick-add-submit:hover:not(:disabled) {
  filter: brightness(1.1);
}

.quick-add-submit:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* ─── Mobile Adjustments ─── */

@media (max-width: 640px) {
  .quick-add-fab {
    bottom: 90px;
    right: 16px;
    width: 52px;
    height: 52px;
  }

  .quick-add-fab-icon {
    font-size: 26px;
  }

  .quick-add-overlay {
    padding-top: 10vh;
    align-items: flex-end;
    padding-bottom: 0;
  }

  .quick-add-modal {
    width: 100%;
    max-width: 100%;
    border-radius: 16px 16px 0 0;
    animation: slideUpMobile 0.25s ease;
  }

  @keyframes slideUpMobile {
    from { 
      transform: translateY(100%);
    }
    to { 
      transform: translateY(0);
    }
  }

  .quick-add-input {
    font-size: 17px;
    padding: 18px 20px;
  }

  .quick-add-contexts {
    padding: 0 16px 14px;
    gap: 6px;
  }

  .quick-add-context {
    padding: 6px 10px;
    font-size: 12px;
  }

  .quick-add-hint {
    display: none;
  }
}

/* ─── Dark Mode ─── */

.dark .quick-add-modal {
  box-shadow: 
    0 24px 48px rgba(0, 0, 0, 0.5),
    0 12px 24px rgba(0, 0, 0, 0.4);
}

.dark .quick-add-overlay {
  background: rgba(0, 0, 0, 0.6);
}
