/* PA Inbox Styles */

.pa-inbox {
  min-height: 100vh;
  background: var(--bg);
  padding: 1.5rem;
  color: var(--text-primary);
  direction: rtl;
  overflow-x: hidden;
  /* Safe area for bottom navigation */
  padding-bottom: 100px;
}

/* Dark mode specific overrides */
.dark .pa-inbox {
  background: linear-gradient(160deg, #0f0f1a 0%, #1a1a2e 100%);
  color: #fff;
}

@media (max-width: 640px) {
  .pa-inbox {
    padding: 1rem;
    padding-bottom: 100px;
  }
}

.pa-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding: 16px;
  padding-right: 60px;
}

.pa-header h1 {
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0;
}

.pa-badge {
  background: var(--accent-soft, rgba(139, 92, 246, 0.2));
  color: var(--accent, #a78bfa);
  padding: 0.35rem 0.75rem;
  border-radius: 20px;
  font-size: 0.85rem;
  white-space: nowrap;
  min-width: fit-content;
}

.dark .pa-badge {
  background: rgba(139, 92, 246, 0.2);
  color: #a78bfa;
}

.pa-filters {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.pa-filter {
  padding: 0.625rem 1rem;
  min-height: 44px;
  background: var(--bg-muted);
  border: 1px solid var(--border);
  border-radius: 20px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
}

.pa-filter:hover {
  background: var(--bg-hover);
}

.pa-filter.active {
  background: var(--accent);
  border-color: var(--accent);
  color: white;
}

/* Dark mode filters */
.dark .pa-filter {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #94a3b8;
}

.dark .pa-filter:hover {
  background: rgba(255, 255, 255, 0.1);
}

.dark .pa-filter.active {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.5);
  color: #a78bfa;
}

.pa-loading,
.pa-empty {
  text-align: center;
  padding: 3rem 1rem;
  color: var(--text-muted);
}

.dark .pa-loading,
.dark .pa-empty {
  color: #64748b;
}

.pa-tasks {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.pa-task {
  background: var(--bg-card);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  box-shadow: var(--shadow-sm);
}

.dark .pa-task {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: none;
}

.pa-task.done {
  opacity: 0.6;
}

.pa-task-main {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  flex: 1;
}

.pa-task-energy {
  font-size: 1.25rem;
}

.pa-task-content {
  flex: 1;
}

.pa-task-text {
  display: block;
  color: var(--text-primary);
  line-height: 1.4;
}

.dark .pa-task-text {
  color: #fff;
}

.pa-task-labels {
  display: flex;
  flex-wrap: wrap;
  gap: 0.35rem;
  margin-top: 0.5rem;
}

.pa-task-label {
  background: var(--bg-muted);
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
  font-size: 0.75rem;
  color: var(--text-muted);
}

.dark .pa-task-label {
  background: rgba(255, 255, 255, 0.1);
  color: #94a3b8;
}

.pa-task-done {
  background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
  border: none;
  color: #fff;
  padding: 0.625rem 1rem;
  min-height: 44px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  white-space: nowrap;
  transition: opacity 0.2s;
  display: flex;
  align-items: center;
}

.pa-task-done:hover {
  opacity: 0.9;
}

.pa-task-closed {
  color: #22c55e;
  font-size: 0.85rem;
  white-space: nowrap;
}

/* Mobile responsive task layout */
@media (max-width: 480px) {
  .pa-task {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
  
  .pa-task-main {
    width: 100%;
  }
  
  .pa-task-done {
    width: 100%;
    justify-content: center;
  }
  
  .pa-task-text {
    word-break: break-word;
  }
}
