/* ═══════════════════════════════════════════
   Auth Page — Golden Premium Login
   ═══════════════════════════════════════════ */

.auth-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
  background: 
    radial-gradient(ellipse at top, rgba(245, 158, 11, 0.04) 0%, transparent 50%),
    radial-gradient(ellipse at bottom, rgba(139, 92, 246, 0.03) 0%, transparent 50%),
    var(--bg);
  direction: rtl;
}

.auth-container {
  width: 100%;
  max-width: 400px;
  animation: authFadeIn 0.4s ease;
}

@keyframes authFadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ─── Header ─── */

.auth-header {
  text-align: center;
  margin-bottom: 40px;
}

.auth-logo {
  font-size: 34px;
  font-weight: 800;
  letter-spacing: -0.03em;
  background: linear-gradient(135deg, var(--text-primary) 0%, var(--text-secondary) 50%, #f59e0b 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0 0 8px;
}

.auth-tagline {
  font-size: 15px;
  color: var(--text-muted);
  margin: 0;
}

/* ─── Social Login ─── */

.auth-social {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 24px;
}

.auth-social-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  width: 100%;
  padding: 14px 24px;
  font-size: 15px;
  font-weight: 500;
  border-radius: var(--radius-sm);
  cursor: pointer;
  transition: all 0.15s ease;
  border: 1px solid var(--border);
  background: var(--bg-card);
  color: var(--text-primary);
}

.auth-social-btn:hover:not(:disabled) {
  background: var(--bg-hover);
  transform: translateY(-1px);
  box-shadow: var(--shadow);
}

.auth-social-btn:active:not(:disabled) {
  transform: translateY(0);
}

.auth-social-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.auth-social-btn svg {
  flex-shrink: 0;
}

/* Google specific */
.auth-google:hover:not(:disabled) {
  border-color: #4285F4;
}

/* Microsoft specific */
.auth-microsoft:hover:not(:disabled) {
  border-color: #00A4EF;
}

/* ─── Divider ─── */

.auth-divider {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 24px;
}

.auth-divider::before,
.auth-divider::after {
  content: '';
  flex: 1;
  height: 1px;
  background: var(--border);
}

.auth-divider span {
  font-size: 13px;
  color: var(--text-muted);
}

/* ─── Form ─── */

.auth-form {
  background: var(--bg-card);
  border: 1px solid var(--border);
  border-radius: 20px;
  padding: 32px;
  box-shadow: 
    0 4px 20px rgba(0, 0, 0, 0.08),
    0 8px 40px rgba(0, 0, 0, 0.04),
    0 0 0 1px rgba(255, 255, 255, 0.05) inset;
}

.auth-field {
  margin-bottom: 20px;
}

.auth-field label {
  display: block;
  font-size: 13px;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.auth-field input {
  width: 100%;
  padding: 12px 16px;
  font-size: 15px;
  border: 1px solid var(--border);
  border-radius: var(--radius-sm);
  background: var(--bg);
  color: var(--text-primary);
  transition: all 0.15s ease;
  direction: ltr;
  text-align: left;
}

.auth-field input::placeholder {
  color: var(--text-muted);
}

.auth-field input:focus {
  outline: none;
  border-color: var(--accent);
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.05);
}

.auth-field input:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* ─── Submit Button ─── */

.auth-submit {
  width: 100%;
  padding: 14px 24px;
  font-size: 15px;
  font-weight: 600;
  color: white;
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  margin-top: 12px;
  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.3);
}

.auth-submit:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(245, 158, 11, 0.4);
}

.auth-submit:active:not(:disabled) {
  transform: translateY(0);
  box-shadow: 0 2px 8px rgba(245, 158, 11, 0.25);
}

.auth-submit:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* ─── Mode Switcher ─── */

.auth-switch {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  margin-top: 24px;
}

.auth-link {
  background: none;
  border: none;
  font-size: 14px;
  color: var(--text-muted);
  cursor: pointer;
  padding: 4px;
  transition: color 0.15s ease;
}

.auth-link:hover {
  color: var(--text-primary);
  text-decoration: underline;
}

/* ─── Dark Mode ─── */

.dark .auth-form {
  background: var(--bg-card);
}

.dark .auth-field input {
  background: var(--bg-muted);
  border-color: var(--border);
}

.dark .auth-field input:focus {
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.05);
}

/* ─── Mobile ─── */

@media (max-width: 480px) {
  .auth-page {
    padding: 16px;
    align-items: flex-start;
    padding-top: 15vh;
  }

  .auth-form {
    padding: 24px;
  }

  .auth-header {
    margin-bottom: 32px;
  }

  .auth-logo {
    font-size: 28px;
  }
}
