/* ═══════════════════════════════════════════
   Calendar Mode — Daily Event View
   ═══════════════════════════════════════════ */

.cal {
  min-height: 100vh;
  padding: 24px 16px;
  padding-bottom: 100px;
  background: var(--bg);
}

.cal-container {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

/* Header */
.cal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 0;
  margin-bottom: 16px;
}

.cal-nav-btn {
  width: 44px;
  height: 44px;
  border: none;
  background: var(--bg-muted);
  border-radius: 12px;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.cal-nav-btn:hover {
  background: var(--bg-card);
  transform: scale(1.05);
}

.cal-date-info {
  text-align: center;
  flex: 1;
}

.cal-date {
  font-size: 20px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.cal-today-btn {
  margin-top: 4px;
  padding: 4px 12px;
  border: none;
  background: var(--accent, #6366f1);
  color: white;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
}

/* Events List */
.cal-events {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.cal-event {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  background: var(--bg-card);
  border: 1px solid var(--border-light);
  border-radius: 12px;
  transition: all 0.2s ease;
}

.cal-event:hover {
  transform: translateX(4px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.cal-event-bar {
  width: 4px;
  height: 40px;
  border-radius: 2px;
  flex-shrink: 0;
}

.cal-event-time {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-secondary);
  min-width: 100px;
}

.cal-event-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.cal-event-title {
  font-size: 15px;
  font-weight: 500;
  color: var(--text-primary);
}

.cal-event-location {
  font-size: 12px;
  color: var(--text-muted);
}

.cal-event-category {
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  padding: 4px 8px;
  border-radius: 10px;
  color: white;
}

/* Empty state */
.cal-empty {
  text-align: center;
  padding: 60px 20px;
  color: var(--text-muted);
}

.cal-empty-icon {
  font-size: 48px;
  display: block;
  margin-bottom: 16px;
  opacity: 0.5;
}

.cal-empty p {
  font-size: 16px;
}

/* Loading */
.cal-loading {
  text-align: center;
  padding: 40px;
  color: var(--text-muted);
}

/* Summary */
.cal-summary {
  text-align: center;
  padding: 16px;
  font-size: 13px;
  color: var(--text-muted);
  margin-top: 16px;
}

/* Responsive */
@media (max-width: 640px) {
  .cal-event {
    flex-wrap: wrap;
    gap: 8px;
  }

  .cal-event-time {
    min-width: auto;
  }

  .cal-event-content {
    width: 100%;
    order: 1;
  }

  .cal-event-category {
    order: 2;
    margin-left: auto;
  }
}
