/* ─── Skeleton Loading ─── */

.skeleton {
  background: linear-gradient(
    90deg,
    var(--bg-muted) 0%,
    var(--bg-hover) 50%,
    var(--bg-muted) 100%
  );
  background-size: 200% 100%;
  animation: skeletonShimmer 1.5s ease-in-out infinite;
  border-radius: var(--radius-sm);
}

@keyframes skeletonShimmer {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

.skeleton-circle {
  border-radius: 50%;
}

.skeleton-pill {
  border-radius: 50px;
}

/* ─── Skeleton Card ─── */

.skeleton-card {
  background: var(--bg-card);
  border: 1px solid var(--border-light);
  border-radius: var(--radius);
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.skeleton-card-header {
  display: flex;
  align-items: center;
  gap: 12px;
}

.skeleton-card-body {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

/* ─── Skeleton List ─── */

.skeleton-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.skeleton-list-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 0;
}

/* ─── Command Mode Skeleton ─── */

.cmd-skeleton {
  display: flex;
  flex-direction: column;
  gap: 48px;
  max-width: 560px;
  margin: 0 auto;
  padding: 40px 24px;
}

.cmd-skeleton-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.cmd-skeleton-cards {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

/* ─── Dark Mode ─── */

.dark .skeleton {
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.05) 0%,
    rgba(255, 255, 255, 0.1) 50%,
    rgba(255, 255, 255, 0.05) 100%
  );
  background-size: 200% 100%;
}
