/* ═══════════════════════════════════════════
   Workbench Mode — Golden Premium Design
   Component styles handled by Golden/Tailwind
   ═══════════════════════════════════════════ */

.wb {
  min-height: 100vh;
  padding: 48px 24px 120px;
  background: 
    radial-gradient(ellipse at bottom right, rgba(59, 130, 246, 0.03) 0%, transparent 50%),
    linear-gradient(180deg, var(--bg) 0%, var(--bg-muted) 100%);
}

.wb-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  gap: 12px;
  color: var(--text-muted);
  font-size: 14px;
}

.wb-pulse {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: var(--text-muted);
  animation: pulse 1.2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 0.4; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.1); }
}

.wb-container {
  max-width: 720px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* ─── Header ─── */

.wb-header {
  text-align: center;
  padding-bottom: 16px;
}

.wb-header h1 {
  font-size: 24px;
  font-weight: 700;
  letter-spacing: -0.01em;
}

.wb-sub {
  color: var(--text-muted);
  font-size: 14px;
  margin-top: 4px;
}

/* ─── Tabs ─── */

.wb-tabs {
  display: flex;
  gap: 4px;
  background: var(--bg-muted);
  border-radius: 14px;
  padding: 5px;
  overflow-x: auto;
  box-shadow: 
    0 0 0 1px rgba(0, 0, 0, 0.04),
    inset 0 1px 2px rgba(0, 0, 0, 0.04);
}

.wb-tab {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  padding: 12px 18px;
  border: none;
  background: transparent;
  border-radius: 10px;
  cursor: pointer;
  font-size: 13px;
  font-weight: 500;
  color: var(--text-muted);
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: var(--font);
  white-space: nowrap;
}

.wb-tab:hover {
  color: var(--text-secondary);
  background: rgba(255,255,255,0.6);
}

.wb-tab-active {
  background: var(--bg-card);
  color: var(--text-primary);
  box-shadow: 
    0 2px 8px rgba(0, 0, 0, 0.06),
    0 0 0 1px rgba(0, 0, 0, 0.04);
}

.wb-tab-count {
  font-size: 11px;
  font-weight: 600;
  background: var(--bg-muted);
  padding: 2px 8px;
  border-radius: 50px;
  color: var(--text-muted);
  transition: all 0.2s ease;
}

.wb-tab-active .wb-tab-count {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: white;
  box-shadow: 0 2px 4px rgba(59, 130, 246, 0.3);
}

/* ─── Drag Handle (for sortable items) ─── */

/* ─── Edit Buttons - 44px touch target ─── */

.wb [data-action="edit"],
.wb [data-action="delete"],
.wb button[aria-label="Edit"],
.wb button[aria-label="Delete"],
.wb [class*="edit-btn"],
.wb [class*="Edit"] button,
.wb button[aria-label*="edit" i],
.wb button[aria-label*="עריכה"] {
  min-width: 44px !important;
  min-height: 44px !important;
  width: 44px !important;
  height: 44px !important;
  padding: 10px !important;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wb-drag-handle {
  cursor: grab;
  user-select: none;
  min-width: 44px;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wb-drag-handle:hover {
  background: var(--bg-hover);
  border-radius: var(--radius-sm);
}

.wb-drag-handle:active {
  cursor: grabbing;
}

/* ─── Task Text Truncation ─── */

.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  word-break: break-word;
}

/* ─── Context Switcher ─── */

.wb-context-switcher {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding: 12px 0;
  margin-bottom: 8px;
  border-bottom: 1px solid var(--border);
}

/* Mobile: Horizontal scroll instead of wrapping */
@media (max-width: 640px) {
  .wb-context-switcher {
    flex-wrap: nowrap;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 16px;
    margin-right: -12px;
    margin-left: -12px;
    padding-right: 12px;
    padding-left: 12px;
    scrollbar-width: none;
    -ms-overflow-style: none;
  }
  
  .wb-context-switcher::-webkit-scrollbar {
    display: none;
  }
  
  .wb-context-btn {
    flex-shrink: 0;
  }
}

.wb-context-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 14px;
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: 10px;
  font-size: 13px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: var(--font);
}

.wb-context-btn:hover {
  background: var(--bg-hover);
  border-color: var(--text-muted);
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}

.wb-context-btn:active {
  transform: translateY(0);
}

.wb-context-active {
  background: linear-gradient(135deg, var(--accent) 0%, var(--accent-soft) 100%);
  border-color: var(--accent);
  color: white;
  box-shadow: 0 2px 8px rgba(59, 130, 246, 0.25);
}

.wb-context-active:hover {
  background: linear-gradient(135deg, var(--accent) 0%, var(--accent-soft) 100%);
  border-color: var(--accent);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.wb-context-icon {
  font-size: 14px;
}

.wb-context-label {
  font-weight: 500;
}

/* ─── Archive Section ─── */

.wb-archive-toggle {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-top: 12px;
  padding: 8px 14px;
  background: var(--bg-muted);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  font-size: 13px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.15s ease;
}

.wb-archive-toggle:hover {
  background: var(--bg-hover);
  color: var(--text);
}

.wb-archive-arrow {
  font-size: 10px;
  opacity: 0.6;
}

.wb-archive {
  background: var(--bg-muted);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 16px;
  margin-bottom: 16px;
}

.wb-archive-header {
  margin-bottom: 12px;
}

.wb-archive-header h3 {
  font-size: 14px;
  font-weight: 500;
  color: var(--text-secondary);
}

.wb-archive-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  max-height: 300px;
  overflow-y: auto;
}

.wb-archive-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 12px;
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: var(--radius-sm);
  gap: 12px;
}

.wb-archive-item-content {
  display: flex;
  align-items: center;
  gap: 10px;
  flex: 1;
  min-width: 0;
}

.wb-archive-status {
  font-size: 14px;
}

.wb-archive-status.completed {
  color: var(--green);
}

.wb-archive-status.archived {
  color: var(--text-muted);
}

.wb-archive-text {
  flex: 1;
  font-size: 13px;
  color: var(--text-secondary);
  text-decoration: line-through;
  opacity: 0.8;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.wb-archive-date {
  font-size: 11px;
  color: var(--text-muted);
  white-space: nowrap;
}

.wb-archive-restore {
  padding: 6px 10px;
  background: transparent;
  border: 1px solid var(--border);
  border-radius: var(--radius-sm);
  font-size: 12px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.15s ease;
  white-space: nowrap;
}

.wb-archive-restore:hover {
  background: var(--blue);
  border-color: var(--blue);
  color: white;
}

/* ─── Responsive ─── */

@media (max-width: 640px) {
  .wb {
    padding: 32px 12px 120px;
    overflow-x: hidden;
  }

  .wb-header h1 {
    font-size: 20px;
  }

  .wb-tab {
    /* 44px minimum touch target */
    min-height: 44px;
    padding: 10px 12px;
    font-size: 12px;
  }
  
  .wb-tabs {
    /* Allow horizontal scroll if needed but hide scrollbar */
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  
  .wb-tabs::-webkit-scrollbar {
    display: none;
  }
}

/* Very small screens */
@media (max-width: 375px) {
  .wb-tab {
    padding: 10px 8px;
    font-size: 11px;
  }
  
  .wb-tab-count {
    font-size: 10px;
    padding: 1px 5px;
  }
}

/* ═══════════════════════════════════════════
   Lanes View (NOW / SOON / LATER)
   ═══════════════════════════════════════════ */

.wb-view-toggle {
  display: flex;
  gap: 4px;
  background: var(--bg-muted);
  border-radius: 10px;
  padding: 4px;
  margin-bottom: 16px;
}

.wb-view-btn {
  flex: 1;
  padding: 8px 16px;
  border: none;
  background: transparent;
  border-radius: 8px;
  font-size: 13px;
  font-weight: 500;
  color: var(--text-muted);
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: var(--font);
}

.wb-view-btn:hover {
  color: var(--text-secondary);
}

.wb-view-active {
  background: var(--bg-card);
  color: var(--text-primary);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.08);
}

/* Lanes Container */
.wb-lanes {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

/* Individual Lane */
.wb-lane {
  background: var(--bg-card);
  border: 1px solid var(--border-light);
  border-radius: 12px;
  overflow: hidden;
}

.wb-lane-header {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background: var(--bg-muted);
  border-left: 4px solid;
  border-bottom: 1px solid var(--border-light);
}

.wb-lane-icon {
  font-size: 14px;
}

.wb-lane-title {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: var(--text-secondary);
}

.wb-lane-count {
  margin-left: auto;
  font-size: 12px;
  font-weight: 600;
  background: var(--bg);
  padding: 2px 8px;
  border-radius: 10px;
  color: var(--text-muted);
}

.wb-lane-tasks {
  padding: 8px;
  min-height: 60px;
}

.wb-lane-empty {
  padding: 20px;
  text-align: center;
  font-size: 13px;
  color: var(--text-muted);
  font-style: italic;
}

/* Mobile: Stack lanes vertically */
@media (max-width: 640px) {
  .wb-lanes {
    gap: 12px;
  }

  .wb-lane-header {
    padding: 10px 12px;
  }

  .wb-lane-tasks {
    padding: 6px;
  }
}
