/* ═══════════════════════════════════════════
   Settings Page — Golden Premium Design
   ═══════════════════════════════════════════ */

.settings-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
  animation: fadeIn 0.2s ease;
}

.settings-modal {
  width: 100%;
  max-width: 520px;
  max-height: 85vh;
  background: var(--bg-card);
  border: 1px solid var(--border);
  border-radius: 20px;
  box-shadow: 
    0 25px 50px -12px rgba(0, 0, 0, 0.25),
    0 0 0 1px rgba(255, 255, 255, 0.05);
  overflow: hidden;
  animation: slideUp 0.25s cubic-bezier(0.16, 1, 0.3, 1);
  display: flex;
  flex-direction: column;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { 
    opacity: 0; 
    transform: translateY(24px) scale(0.96);
  }
  to { 
    opacity: 1; 
    transform: translateY(0) scale(1);
  }
}

/* ─── Header ─── */

.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 24px 28px 20px;
  border-bottom: 1px solid var(--border);
  background: linear-gradient(180deg, var(--bg-card) 0%, transparent 100%);
}

.settings-header-content {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.settings-header h2 {
  font-size: 20px;
  font-weight: 700;
  margin: 0;
  color: var(--text-primary);
  letter-spacing: -0.02em;
}

.settings-header-sub {
  font-size: 13px;
  color: var(--text-muted);
  font-weight: 400;
}

.settings-close {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  color: var(--text-muted);
  cursor: pointer;
  transition: all 0.15s ease;
}

.settings-close:hover {
  background: var(--bg-muted);
  color: var(--text-primary);
  transform: scale(1.05);
}

.settings-close:active {
  transform: scale(0.95);
}

/* ─── Content ─── */

.settings-content {
  padding: 24px 28px;
  overflow-y: auto;
  direction: rtl;
}

/* ─── Section ─── */

.settings-section {
  margin-bottom: 32px;
}

.settings-section:last-child {
  margin-bottom: 0;
}

.settings-section-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 8px;
}

.settings-section h3 {
  font-size: 15px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.settings-section-badge {
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  padding: 3px 8px;
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  color: white;
  border-radius: 50px;
}

.settings-desc {
  font-size: 13px;
  color: var(--text-muted);
  margin: 0 0 16px;
  line-height: 1.6;
}

/* ─── Integrations ─── */

.settings-integrations {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.settings-integration {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 18px;
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: 14px;
  transition: all 0.2s ease;
}

.settings-integration:hover {
  border-color: var(--border-light);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}

.settings-integration-connected {
  background: linear-gradient(135deg, var(--green-soft) 0%, rgba(34, 197, 94, 0.05) 100%);
  border-color: rgba(34, 197, 94, 0.3);
}

.settings-integration-pending {
  opacity: 0.85;
  background: linear-gradient(135deg, var(--bg) 0%, var(--bg-muted) 100%);
}

.settings-integration-info {
  display: flex;
  align-items: center;
  gap: 14px;
}

.settings-integration-icon {
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  border-radius: 12px;
  box-shadow: 
    0 2px 8px rgba(0, 0, 0, 0.06),
    0 0 0 1px rgba(0, 0, 0, 0.04);
  transition: transform 0.15s ease;
}

.settings-integration:hover .settings-integration-icon {
  transform: scale(1.05);
}

.settings-integration-icon svg {
  width: 24px;
  height: 24px;
}

.settings-integration-text {
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.settings-integration-name {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
}

.settings-integration-status {
  font-size: 12px;
  color: var(--text-muted);
  display: flex;
  align-items: center;
  gap: 6px;
}

.settings-integration-status.connected {
  color: var(--green);
  font-weight: 500;
}

.settings-integration-status.pending {
  color: var(--amber);
}

.settings-status-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: var(--green);
  animation: statusPulse 2s ease-in-out infinite;
}

.settings-status-dot.pending {
  background: var(--amber);
  animation: statusPulse 3s ease-in-out infinite;
}

@keyframes statusPulse {
  0%, 100% { opacity: 0.6; }
  50% { opacity: 1; }
}

.settings-last-sync {
  color: var(--text-muted);
  font-weight: 400;
  margin-right: 4px;
}

/* ─── Buttons ─── */

.settings-btn {
  padding: 10px 20px;
  font-size: 13px;
  font-weight: 600;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
  font-family: var(--font);
  min-height: 40px;
}

.settings-btn-connect {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: white;
  box-shadow: 0 2px 8px rgba(59, 130, 246, 0.25);
}

.settings-btn-connect:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.35);
}

.settings-btn-connect:active {
  transform: translateY(0);
}

.settings-btn-connect:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.settings-btn-pending {
  background: var(--bg-muted);
  color: var(--text-muted);
  border: 1px solid var(--border);
}

.settings-btn-pending:hover {
  background: var(--bg-hover);
  color: var(--text-secondary);
}

.settings-btn-disconnect {
  background: transparent;
  border: 1px solid var(--border);
  color: var(--text-secondary);
}

.settings-btn-disconnect:hover {
  background: var(--red-soft);
  border-color: var(--red);
  color: var(--red);
}

.settings-btn-danger {
  background: transparent;
  color: var(--red);
  border: 1px solid var(--red);
}

.settings-btn-danger:hover {
  background: var(--red);
  color: white;
}

/* ─── Info Banner ─── */

.settings-info-banner {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-top: 16px;
  padding: 14px 16px;
  background: linear-gradient(135deg, var(--amber-soft) 0%, rgba(245, 158, 11, 0.08) 100%);
  border: 1px solid rgba(245, 158, 11, 0.2);
  border-radius: 12px;
}

.settings-info-icon {
  font-size: 18px;
  line-height: 1;
}

.settings-info-banner p {
  font-size: 13px;
  color: var(--text-secondary);
  line-height: 1.5;
  margin: 0;
}

/* ─── Preferences ─── */

.settings-preferences {
  display: flex;
  flex-direction: column;
  gap: 1px;
  background: var(--border);
  border-radius: 12px;
  overflow: hidden;
}

.settings-preference-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 16px;
  background: var(--bg);
}

.settings-preference-info {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.settings-preference-label {
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
}

.settings-preference-desc {
  font-size: 12px;
  color: var(--text-muted);
}

.settings-preference-value {
  font-size: 13px;
  color: var(--text-secondary);
  font-weight: 500;
}

/* ─── Account ─── */

.settings-account {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 18px;
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: 14px;
}

.settings-account-info {
  display: flex;
  align-items: center;
  gap: 14px;
}

.settings-account-avatar {
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, var(--accent) 0%, var(--accent-soft) 100%);
  color: white;
  font-size: 18px;
  font-weight: 700;
  border-radius: 12px;
}

.settings-account-details {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.settings-account-name {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
}

.settings-email {
  font-size: 12px;
  color: var(--text-muted);
  direction: ltr;
  text-align: right;
}

/* ─── Footer ─── */

.settings-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  margin-top: 24px;
  border-top: 1px solid var(--border);
}

.settings-version {
  font-size: 11px;
  color: var(--text-muted);
  font-weight: 500;
}

.settings-built {
  font-size: 11px;
  color: var(--text-muted);
}

/* ─── Mobile ─── */

@media (max-width: 480px) {
  .settings-overlay {
    padding: 0;
    align-items: flex-end;
  }

  .settings-modal {
    max-width: 100%;
    max-height: 92vh;
    border-radius: 20px 20px 0 0;
    animation: slideUpMobile 0.3s cubic-bezier(0.16, 1, 0.3, 1);
  }

  @keyframes slideUpMobile {
    from { transform: translateY(100%); }
    to { transform: translateY(0); }
  }

  .settings-header {
    padding: 20px 20px 16px;
  }

  .settings-content {
    padding: 20px;
  }

  .settings-integration {
    flex-direction: column;
    align-items: stretch;
    gap: 14px;
  }

  .settings-integration-info {
    width: 100%;
  }

  .settings-btn {
    width: 100%;
    justify-content: center;
  }
}

/* ─── Dark Mode ─── */

.dark .settings-integration-icon {
  background: var(--bg-muted);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.dark .settings-modal {
  box-shadow: 
    0 25px 50px -12px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(255, 255, 255, 0.05);
}

.dark .settings-overlay {
  background: rgba(0, 0, 0, 0.75);
}
